.linkContainer {
  display: flex;
  margin: auto;
  list-style: none;
}
.linkWrapper {
  text-transform: uppercase;
  font-weight: 800;
  padding: 0 18px;
  margin: 0 auto;
  cursor: pointer;
}
.menu {
  font-family: Roboto, Trebuchet MS, Helvetica, Arial, sans-serif;
  background-color: #202020;
  color: #fff;
}
* {
  padding: 0;
  margin: 0;
}
a {
  color: #fff;
}
.nav_upper,
.nav_lower,
.tabAndLogoContainer {
  display: flex;
  width: 100%;
  justify-content: space-between;
  box-sizing: border-box;
}
.nav_link,
.right-link,
.user_link {
  font: 400 12px/1 RobotoCondensed Bold, Trebuchet MS, Helvetica, Arial,
    sans-serif;
  letter-spacing: 1px;
  text-transform: uppercase;
  font-weight: bold;
}
.nav_lower {
  height: 40px;
}
.img_user,
.searchPromo_img,
.img_search,
.img_logo,
.promo_img {
  width: 100%;
}
.img_user {
  border-radius: 50%;
}
.logo {
  margin: 10px;
  width: 150px;
}
.nav_upper {
  padding: 0 50px;
  border-bottom: 1px solid #393939;
}
.tabContainer,
.insider_tabContainer {
  display: flex;
  align-items: center;
  padding: 0px 10px;
}
.searchPromo_tab {
  display: flex;
}
.icon_u,
.user_avatar,
.icon_s {
  width: 40px;
}
.searchPromo_tab {
  display: flex;
  align-items: center;
}
.insider_tabContainer,
.tabContainer {
  border-left: 2px solid #393939;
  border-right: 2px solid #393939;
}
.search_tabContainer {
  border-right: 2px solid #393939;
}
.right-link {
  display: flex;
}
.search_tabContainer {
  display: flex;
  align-items: center;
}
.user_avatar,
.icon_u {
  margin-right: 10px;
}
.searchPromo_wrap {
  text-align: center;
}
.promo,
.promo_content {
  color: #fff;
  background-color: #151515;
  display: flex;
  align-items: center;
  justify-content: center;
}
.promo_logo {
  margin: 0 0 0 10px;
}
.promo_title {
  padding: 0;
  margin: 0;
  font: 400 14px/1 RobotoCondensed, Trebuchet MS, Helvetica, Arial, sans-serif;
  text-transform: uppercase;
  font-weight: bold;
}
.promo_img {
  width: 70px;
}
.masthead_miniBackground {
  position: relative;
  width: 100%;
  margin: 0;
  height: auto;
  background-color: transparent;
  min-height: 400px;
}
.built_background {
  padding: 200px 0px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  border-bottom: 20px solid #151515;
}
.masthead_hero {
  display: flex;
  position: absolute;
  top: 50%;
  right: 0;
  left: 0;
  background-color: transparent;
}
.masthead_main {
  height: 100%;
  text-align: center;
  position: relative;
  width: 100%;
  z-index: 50;
}
.masthead_headline,
.masthead_copy {
  display: block;
  color: #fff;
}
.masthead_headline {
  font: 500 40px/1 RobotoCondensed Bold, Trebuchet MS, Helvetica, Arial,
    sans-serif;
  line-height: 1;
  text-transform: uppercase;
  transition: font-size 0.2s ease;
  font-weight: bold;
}
.masthead_copy {
  font: 400 16px/1.3 Roboto, Trebuchet MS, Helvetica, Arial, sans-serif;
  letter-spacing: 0.5px;
}
.content-grid_3 {
  background-color: #fff;
}
.content-grid {
  max-width: 1240px;
  padding: 10px 20px 0;
  margin: 0 auto;
}
.inlineModuleHeader {
  display: flex;
  position: relative;
  width: 100%;
  align-items: baseline;
  flex-wrap: wrap;
  justify-content: space-between;
}
h3 {
  font: 800 26px/1 RobotoCondensed, Trebuchet MS, Helvetica, Arial, sans-serif;
  letter-spacing: 1px;
  text-transform: uppercase;
}
h3 {
  padding: 0;
  font-weight: 400;
}
.inlineModuleHeader_innerComponent {
  display: flex;
  flex: 1 0 auto;
  justify-content: flex-end;
}
div {
  margin: 0;
  padding: 0;
}
.grid-base-6 {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(15.82%, 1fr));
  transition: max-height 1s ease-out;
  overflow: hidden;
  grid-column-gap: 10px;
}
.mvl-card {
  margin: 6px 0 4px;
}
.mvl-card {
  color: #fff;
  transition: color 0.3s;
  box-sizing: border-box;
  position: relative;
}
.card-thumb {
  background: #e62429;
  margin: 0;
  overflow: hidden;
  padding: 0;
}
.explore_link {
  text-decoration: none;
}
@media (min-width: 601px) {
  .img__wrapper .card-thumb-frame .mvl-card {
    height: 210px;
  }
}
.img__wrapper .card-thumb-frame .mvl-card {
  position: relative;
  overflow: hidden;
}
.card-thumb-frame {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.card-body {
  padding: 16px 24px 17px 20px;
}
.card-body {
  background: #151515;
  transition: color 0.3s;
  height: 130px;
}
.card-body_headline {
  font: 400 16px/1.1 RobotoCondensed Bold, Trebuchet MS, Helvetica, Arial,
    sans-serif;
  letter-spacing: 1px;
  text-transform: uppercase;
}
.card-footer_secondery_text {
  letter-spacing: 1px;
  text-transform: uppercase;
  font: 400 12px/1 RobotoCondensed Bold, Trebuchet MS, Helvetica, Arial,
    sans-serif;
  color: #bbb;
}
.card-footer {
  bottom: 17px;
  position: absolute;
  padding-right: 10px;
  text-align: left;
}
.mvl-card,
.card-body {
  overflow: hidden;
  position: relative;
  line-height: 1;
}

.main-footer {
  background: #151515;
  padding-top: 80px;
  padding-bottom: 30px;
}
.main-footer__full-content {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  padding-right: 20px;
  padding-left: 20px;
}
.main-footer__links {
  display: flex;
  flex-wrap: nowrap;
  padding-bottom: 0;
  border-bottom: 0;
  margin-bottom: 40px;
}
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
.in,
.out {
  width: 60px;
  margin-right: 20px;
}

.main-footer__link {
  display: block;
  font-size: 13px;
  font-weight: lighter;
  letter-spacing: 1px;
  text-transform: uppercase;
  color: white;
  margin-bottom: 12px;
}
.main-footer__promotion-title,
.main-footer__title {
  font-family: RobotoCondensed Bold, Trebuchet MS, Helvetica, Arial, sans-serif;
  font-size: 14px;
  letter-spacing: 2px;
  line-height: 1;
  margin: 0;
  color: #fff;
  text-align: left;
  text-transform: uppercase;
}

.main-footer__promotion-description {
  color: #767676;
  font-family: Roboto, Trebuchet MS, Helvetica, Arial, sans-serif;
  font-size: 14px;
  margin: 0;
}

.main-footer__promotion-link {
  display: flex;
  text-decoration: none;
  align-items: center;
}

.main-footer__full-content {
  display: flex;
}
.main-footer__logo {
  display: flex;
  justify-content: left;
  margin-right: 60px;
  width: 80px;
  flex: none;
  margin-bottom: 30px;
}
.footer_logo {
  width: 100%;
}
.main-footer__primary-links,
.main-footer__secondary-links {
  font-family: Roboto, Trebuchet MS, Helvetica, Arial, sans-serif;
  font-weight: 800;
  margin-right: 60px;
}
.main-footer__promo-links {
  display: block;
  border-bottom: 0;
  padding: 0;
  margin-left: 40px;
  margin-right: 40px;
  margin-bottom: 40px;
}
.masthead_wrapper_detail {
  background: black;
  display: flex;
  height: 420px;
}
.headline {
  font-size: 50px;
  color: #fff;
  font: 500 26 px / 1 RobotoCondensed Bold, Trebuchet MS, Helvetica, Arial,
    sans-serif;
  line-height: 1;
  text-transform: uppercase;
}
.masthead_container_detail {
  padding: 0 30px 20px;
}
.masthead_main_detail {
  display: flex;
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
  height: 100%;
  padding-left: 50px;
}
.masthead_background_wrapper_detail {
  padding-left: 150px;
}
.img_hero {
  height: 100%;
}
.detail_content {
  display: flex;
  padding: 50px 50px 50px 50px;
}
.content {
  display: flex;
}
.text {
  padding: 10px 0px 0px 100px;
  font: 400 14px / 1.5 Open Sans, Trebuchet MS, Helvetica, Arial, sans-serif;
}
.main-footer__legal-wrapper {
  margin-top: 30px;
}
.main-footer__legal-main {
  display: flex;
  line-height: 1;
  font-family: "Roboto", "Trebuchet MS", Helvetica, Arial, sans-serif;
  font-weight: 400;
  justify-content: center;
}
.main-footer__legal-text {
  color: #767676;
  font-size: 12px;
  text-decoration: none;
  line-height: 1.2em;
  padding: 0 10px;
  margin-bottom: 10px;
}
.main-footer__follow {
  border-bottom: 0;
  padding: 0;
  flex-basis: 220px;
  margin-bottom: 40px;
}
.footer_social_img {
  max-width: 20px;
  margin-top: 20px;
  flex: 0 0 25%;
  grid-row: 1;
  margin-right: 20px;
}
.social_img {
  width: 100%;
}
.footer_social {
  display: flex;
  justify-content: initial;
  flex-wrap: wrap;
  text-align: left;
}
.title {
  width: 100px;
}
.story {
  padding: 10px 0px 0px 100px;
  font: 400 14px / 1.5 Open Sans, Trebuchet MS, Helvetica, Arial, sans-serif;
  width: 300px;
}
